@import 'src/components/styles/helpers.scss';


.row {
  display: flex;
  justify-content: space-between;
}

.col {

  &:nth-child(2) {
    width: rem(1451px);
  }
}

.line {

  &:not(:first-child) {
    margin-top: rem(120px);
  }
}

.grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  column-gap: rem(9px);
}

.item {
  width: rem(283px);
  height: rem(45px);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;

  overflow: hidden;

  &.big {
    height: rem(74px);
  }
}

.image {
  mix-blend-mode: darken;
}