@import 'src/components/styles/helpers.scss';

.row {
  position: relative;
  z-index: 0;

  display: flex;
}

.col {
  flex-shrink: 0;

  &:nth-child(1) {
    position: absolute;
    left: 0;
    top: 0;

    width: calc(#{$col-part-d} * 3);
  }

  &:nth-child(2) {
    padding-left: 25.3%;
    width: 100%;
    // width: calc(#{$col-part-d} * 9 - #{$col-gap-d});
    // width: 74.7%;
  }
}

.item {

  &:not(:first-child) {
    margin-top: rem(12px);
  }
}

.link {
  @include fsl(20px, 100%);
  letter-spacing: -0.01em;
  border-bottom: 1px solid color(black);
}

.container {
  position: relative;
  z-index: 0;

  height: calc(100vh - 64px);
  
  background: color(black);
}

.wrapper {
  position: absolute;
  left: rem(32px);
  top: rem(24px);
  bottom: rem(32px);
  z-index: 1;

  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
}

.label {
  position: sticky;
  position: -webkit-sticky;
  bottom: rem(32px);
  z-index: 1;

  display: flex;
  align-items: center;
}

.icon {
  margin-right: rem(8px);

  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    width: rem(37px);
    height: rem(27px);
  }
}

.text {
  color: color(white);
  @include fsl(72px, 100%);
  letter-spacing: -0.04em;
}