@import 'src/components/styles/helpers.scss';

.modal {
  --pad: 0;

  position: fixed;
  top: var(--pad);
  right: var(--pad);
  bottom: var(--pad);
  left: var(--pad);
  padding: rem(32px);
  z-index: 1000;

  opacity: 0;
  pointer-events: none;
  transition: $transition;

  &.active {
    opacity: 1;
    pointer-events: all;
  }
}

.container {
  position: relative;
  z-index: 0;
  padding: rem(32px);

  height: 100%;

  background: color(white);
}

.close {
  --pad: #{rem(17px)};

  position: absolute;
  top: var(--pad);
  right: var(--pad);
  z-index: 1;

  display: flex;
  align-items: center;
  justify-content: center;

  cursor: pointer;
  outline: none;
  border: none;
  background: none;

  svg {
    --size: #{rem(45px)};

    width: var(--size);
    height: var(--size);
  }
}

.row {
  height: 100%;
  display: flex;
  justify-content: space-between;
}

.col {
  flex-shrink: 0;
  width: calc(50% - 16px);

  &:nth-child(2) {
    display: flex;
    flex-direction: column;
  }
}

.title {
  letter-spacing: -0.03em;
  @include fsl(160px, 85%);
}

.text {
  max-width: rem(686px);

  letter-spacing: -0.01em;
  @include fsl(24px, 130%);
}

.form {
  width: calc(#{$col-part-d} * 5 - #{$col-gap-d});
  height: 100%;
  display: flex;
  align-items: center;
}