@import 'src/components/styles/helpers.scss';

.layout {
  position: fixed;
  z-index: -1;
  left: 0;
  top: 0;

  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  pointer-events: none;
}

.video {
  position: absolute;

  display: block;
  // width: 100%;
  // height: 100%;
}

.test {
  position: fixed;
  z-index: 1;
  top: 100px;
  left: 100px;

  pointer-events: all;
}

.title {
  margin-bottom: 10px;
}

.buttons {
  display: flex;
  gap: 10px;
}

.btn {
  padding: 10px 15px;

  cursor: pointer;
}

.wrapper {
  display: contents;
}
.qwe {
  display: contents;
}