@import 'src/components/styles/helpers.scss';

.header {
  position: fixed;
  z-index: 999;
  left: 0;
  right: 0;
  top: 0;

  mix-blend-mode: difference;
}

.inner {
  padding: rem(32px) 0 rem(52px);
  @media #{media("<")} {
    padding: rem(16px) 0;
  }
}

.row {
  display: flex;
  @media #{media("<")} {
    justify-content: space-between;
  }
}

.col {
  flex-shrink: 0;

  &:nth-child(1) {
    width: calc(#{$col-part-d} * 3);
    @media #{media("<")} {
      width: auto;
    }
  }

  &:nth-child(2) {
    width: calc(#{$col-part-d} * 5 - #{$col-gap-d});
    @media #{media("<")} {
      width: auto;
    }
  }

  &:nth-child(3) {
    flex-grow: 1;

    display: flex;
    justify-content: space-between;
  }
}

.logo {
  display: flex;
  align-items: center;
  justify-content: flex-start;

  svg {
    width: rem(205px);
    height: rem(29px);
    @media #{media("<")} {
      width: rem(146px);
      height: rem(20px);
    }
  }
}

.list {
  display: flex;
  flex-wrap: wrap;
  gap: rem(32px);
}

.link {
  position: relative;
  z-index: 0;

  &::before {
    content: "";
    position: absolute;
    top: calc(100% + 1px);
    left: 0;

    width: 100%;
    height: 1px;

    background: color(white);
    pointer-events: none;
    transform-origin: 100% 50%;
    transform: scale3d(0, 1, 1);
    transition: transform 0.3s;
  }

  &:hover::before {
    transform-origin: 0% 50%;
    transform: scale3d(1, 1, 1);
  }
}

.text {
  @include fsl(20px, 100%);
  color: color(white);
  letter-spacing: -0.01em;
}

.textUs {
  margin-bottom: 5px;

  display: flex;
  align-items: center;
}

.icon {

  width: $col-gap-d;
  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    width: rem(17px);
    height: rem(14px);
  }
}

.wrapper {
  margin-left: $col-gap-d;
}

.desktop {
  @media #{media("<")} {
    display: none;
  }
}

.mobile {
  @media #{media(">")} {
    display: none;
  }
}

.menu {
  width: rem(44px);
  height: rem(10px);
  display: flex;
  align-items: center;
  justify-content: center;

  cursor: pointer;
}

.burger {
  position: relative;
  z-index: 0;

  width: 100%;
  height: 100%;

  border-top: 1px solid color(white);
  border-bottom: 1px solid color(white);
}
