@import 'src/components/styles/helpers.scss';

.form {
  width: 100%;
}

.bot {
  margin-top: rem(34px);

  display: flex;
  justify-content: space-between;
}

.item {

  &:not(:first-child) {
    margin-top: rem(44px);
  }
}

.input {
  padding-bottom: rem(20px);

  width: 100%;

  border: none;
  color: color(black);
  border-bottom: 1px solid color(borders);

  @include placeholder {
    color: color(black);
  }
}