@import 'src/components/styles/helpers.scss';

.container {
  display: flex;
  align-items: center;
  height: rem(561px);
  @media #{media("<")} {
    // height: rem(224px);
    height: auto;
  }
}

.bot {
  margin-top: rem(20px);
  @media #{media("<")} {
    margin-top: rem(12px);
  }
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: $col-gap-d;
  @media #{media("<")} {
    display: block;
  }
}

.col {
  &:nth-child(2) {
    @media #{media("<")} {
      margin-top: rem(4px);
    }
  }
}

.title {
  @include fsl(32px, 100%);
  letter-spacing: -0.04em;
  @media #{media("<")} {
    @include fsl(20px);
  }
}

.list {
  margin-top: rem(20px);
  @media #{media("<")} {
    margin-top: rem(12px);
  }
}