@import 'src/components/styles/helpers.scss';

.intro {
  position: relative;
  z-index: 0;
}

.bg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;

  width: 100%;
  height: 100vh;

  // background: beige;
}

.list {
  position: absolute;
  top: 22.9vh;
  left: calc(#{$col-part-d} * 8 + #{$col-pad-d});

  counter-reset: index;
}

.item {
  position: relative;
  z-index: 0;
  padding-top: rem(6px);
  padding-left: rem(14px);

  @include fsl(32px);
  letter-spacing: -0.04em;
  counter-increment: index;

  &::before {
    content: "0" counter(index);
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;

    @include fsl(12px);
  }

  &:not(:first-child) {
    margin-top: rem(12px);
  }
}

.bot {
  padding-top: 44.7vh;
}

.showreel {
  margin-top: rem(45px);
}

.title {
  @include fsl(210px, 85%);
  letter-spacing: -0.03em;
  
}

.flex {
  display: flex;
  perspective: 40rem;
  perspective-origin: -10% 50%;
}
